@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  width: auto;
  height: auto;
  background-color: #FDC73D;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 1.5%;
}
 body h2 {
  color: #F9665F;
  font-family: 'Oswald', sans-serif;
  font-size: 4em;
  font-weight: 600;
  text-shadow: 0.055em 0 0 white;
  word-break: normal;
  margin: 0;
  padding: 1.5em;
 }

 *::-moz-selection {
   background: none;
   color:#de4343;
 }

 *::selection {
   background: none;
   color:#de4343;
 }

div p {
   display: flex;
   justify-content: center;
   color: #F9665F;
   font-weight: 600;
   text-shadow: 0.1em 0 0 white;
   font-family: 'Oswald', sans-serif;
   font-size: 24px;
 }

 #weather {
   margin: 50px;
   padding: 20px;
   background-color: #F9665F;
   font-family: 'Oswald', sans-serif;
   border-radius: 10em;
   box-shadow: inset 0 0.5em 0 white;
 }
 #weather > p {
   color:white;
   font-size: 20px;
   word-break: break-all;
   text-shadow: none;
 }
 

 form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

button {
  width: 10em;
  height: 2.5em;
  background-color: #F9665F;
  border: none;
  border-radius: 10em;
  box-shadow: inset 0 0.2em 0 white;
  color: white;
  font-family: 'Oswald', sans-serif;
  margin-bottom: 50px;
  margin-top: 20px;
  font-size: 20px;
}

button:hover {
  background-color: #e85858;
}

button:active, :focus {
  background-color: #de4343;
  outline: none;
}

input {
  width: 10em;;
  background-color: #F9665F;
  border-radius: 10em;
  height: 2.5em;
  border:none;
  box-shadow: inset 0 0.2em 0 white;
  text-indent: 0.7em;
  font-size: 19px;
  color:white;
  font-family: 'Oswald', sans-serif;
}

input:hover {
  background-color: #e85858;
}

input:focus {
  background-color: #de4343;
}

::-webkit-input-placeholder {
  color:white;
}

::-moz-placeholder {
  color:white;
}

:-ms-input-placeholder {
  color:white;
}

::-ms-input-placeholder {
  color:white;
}

::placeholder {
  color:white;
}
